/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import React from 'react';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql } from 'gatsby';

import SEO from '../../utils/seo';
// import { useIntl } from "gatsby-plugin-intl"
// import { useTranslations } from "gatsby-plugin-translate"

// Components
import {
  Header,
  Footer,
  GlobalStyles,
  EndHero,
  Hero,
  Image,
} from '../../components';

export const Layout = ({ pageContext = {}, children }) => {
  const { template, content, title } = pageContext;

  const data = useStaticQuery(graphql`
    query SiteTitleQuery2 {
      site {
        siteMetadata {
          title
        }
      }
    }
  `);

  // const t = useTranslations() //useIntl().formatMessage;
  const isHomepage = template === 'home';
  const heroImagePath = content && content.hero.imagePath
    ? Image({ src: content.hero.imagePath, linkOnly: true })
    : '';
  const endHeroImagePath = content && content.endHero.imagePath
    ? Image({ src: content.endHero.imagePath, linkOnly: true })
    : '';

  return (
    <>
      <SEO title={pageContext.title} description={pageContext.description} />
      <Header darkTheme={!isHomepage} />
      <GlobalStyles isHomepage={isHomepage} />
      {!isHomepage && content && content.hero && (
        <Hero
          title={content.hero.title}
          subtitle={title}
          secondaryTitle={content.hero.secondaryTitle}
          text={content.hero.text}
          imagePath={heroImagePath.src}
        />
      )}
      <main>
        {children}
        {content && content.endHero && (
          <EndHero
            title={content.endHero.title}
            text={content.endHero.text}
            cta={content.endHero.cta}
            imagePath={endHeroImagePath.src}
          />
        )}
      </main>
      <Footer siteTitle={data.site.siteMetadata.title} />
    </>
  );
};

Layout.propTypes = {
  pageContext: PropTypes.object.isRequired,
};
