import React from 'react';
import PropTypes from 'prop-types';

// Components
import { Button, Image } from '../../components';
import { StyledServiceCard, StyledImageWrapper, StyledAspectRatio } from './styles';

export const ServiceCard = ({
  imagePath,
  title,
  text,
  reversed,
  cta: { src, label } = {},
  ...props
}) => {
  return (
    <StyledServiceCard reversed={reversed} {...props}>
      {imagePath && (
        <StyledImageWrapper>
          <StyledAspectRatio>
            <Image src={imagePath} />
          </StyledAspectRatio>
        </StyledImageWrapper>
      )}
      {title && <h3>{title}</h3>}
      {text && <p>{text}</p>}
      {src && label && (
        <Button
          to={src}
          variant="link"
          reversed={reversed}
          gutter={{ top: '1rem' }}>
          {label}
        </Button>
      )}
    </StyledServiceCard>
  );
};

ServiceCard.propTypes = {
  reversed: PropTypes.bool,
  title: PropTypes.string,
  text: PropTypes.string,
  imagePath: PropTypes.string,
  cta: PropTypes.shape({
    src: PropTypes.string,
    label: PropTypes.string,
  }),
};
