import React from 'react';
import PropTypes from 'prop-types';

import { Button } from '../../components';
import { StyledStatement, StyledTitle, StyledBody } from './styles';

export const Statement = ({
  children,
  title,
  titleImgSrc,
  condensed = false,
  removeMobileStyles = false,
  maxWidth = 'none',
  gutter = {},
  styleType = 'default',
  cta: { src, label } = {},
  ...props
}) => {
  return (
    <StyledStatement
      {...{ condensed, removeMobileStyles, maxWidth, gutter, styleType }}
      {...props}>
      {title && (
        <StyledTitle {...{ condensed }}>{titleImgSrc || title}</StyledTitle>
      )}
      <StyledBody {...{ condensed }}>
        {children}
        {src && label && (
          <Button
            to={src}
            variant="outline"
            reversed={styleType === 'reversed'}
            gutter={{ top: '1.5rem' }}>
            {label}
          </Button>
        )}
      </StyledBody>
    </StyledStatement>
  );
};

Statement.propTypes = {
  title: PropTypes.string,
  titleImgSrc: PropTypes.string,
  children: PropTypes.any,
  maxWidth: PropTypes.string,
  condensed: PropTypes.bool,
  gutter: PropTypes.object,
  styleType: PropTypes.oneOf(['default', 'dark', 'cta']),
  cta: PropTypes.shape({
    src: PropTypes.string,
    label: PropTypes.string,
  }),
  removeMobileStyles: PropTypes.bool,
};
