import styled from 'styled-components';
import { colors, font, media } from '../../constants/theme';

export const StyledStatement = styled.div`
  margin-right: auto;
  margin-left: auto;
  ${({ maxWidth }) => maxWidth && `max-width: ${maxWidth};`}

  @media ${media.notMobile} {
    display: flex;
    padding: 5rem;
    border-radius: 24px;
  }

  ${({ styleType }) => {
    switch (styleType) {
      case 'dark':
        return `
          background-color: rgba(9, 16, 55, 0.75);
          color: #fff;
        `;
      case 'cta':
        return `
          background-color: ${colors.brandLight};
          color: ${colors.brand};
        `;
      default:
        return `
          background-color: #fff;
          color: ${colors.text};
        `;
    }
  }};

  @media ${media.mobile} {
    ${({ removeMobileStyles }) =>
      removeMobileStyles
        ? `background-color: transparent !important;`
        : `
        padding: 3rem;
        border-radius: 20px;
        
    `}
  }

  /* Gutter */
  ${({ gutter }) => gutter.top && `margin-top: ${gutter.top};`}
  ${({ gutter }) => gutter.bottom && `margin-bottom: ${gutter.bottom};`}
`;

export const StyledTitle = styled.h2`
  font-size: ${({ condensed }) => (condensed ? '20px' : '30px')};
  margin-bottom: 2rem;

  @media ${media.notMobile} {
    flex-shrink: 0;
    max-width: 40%;
    width: ${({ condensed }) => (condensed ? '200px' : '340px')};
    font-size: ${({ condensed }) => (condensed ? '24px' : '38px')};
    margin-right: 2rem;
  }
`;

export const StyledBody = styled.div`
  font-weight: ${font.weight.thin};
`;
