import styled, { css } from 'styled-components';
import { media, colors } from '../../constants/theme';
import { Statement } from '../statement';

export const StyledHero = styled.header`
  color: #fff;
  position: relative;

  @media ${media.notMobile} {
    height: 600px;
  }
  @media ${media.mobile} {
    height: auto;
  }

  @media ${media.desktop} {
    &:before {
      content: '';
      position: absolute;
      top: 0;
      width: 100%;
      height: 400px;
      background-color: ${colors.brand};
      z-index: -2;
    }
  }
`;

export const StyleHeaderWrapper = styled.div`
  background-color: ${colors.brandDark};
  overflow: hidden;
  z-index: -1;

  @media ${media.mobile} {
    padding: 6rem 24px;
  }

  @media ${media.notMobile} {
    padding: 6rem 4.5rem 12rem;
  }

  @media ${media.desktop} {
    border-radius: 25px;
  }

  // offset container margin
  @media ${media.notDesktop} {
    margin-left: -24px;
    margin-right: -24px;
  }

  ${({ imagePath }) =>
    imagePath &&
    css`
      background-image: url('${imagePath}');
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      position: relative;

      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        z-index: -1;
      }
    `}

  > div {
    z-index: 5;
    @media ${media.notMobile} {
      min-height: 218px;
    }
  }

  h2 {
    text-transform: uppercase;
  }

  @media ${media.notMobile} {
    h1 {
      font-size: 44px;
      max-width: 600px;
      z-index: 4;
    }
    h2 {
      font-size: 18px;
      margin-bottom: 1.5rem;
      z-index: 4;
    }
  }

  @media ${media.mobile} {
    h1 {
      font-size: 26px;
    }
    h2 {
      font-size: 14px;
      margin-bottom: 1rem;
    }
  }
`;

export const StyledStatement = styled(Statement)`
  z-index: 9;

  @media ${media.desktop} {
    max-width: 70%;
    min-width: 840px;
  }
  @media ${media.notMobile} {
    margin-top: -8rem;
  }
  @media ${media.mobile} {
    margin: 4rem 0 5rem;

    h2 {
      font-size: 24px;
    }
  }
`;
