import React from 'react';
import PropTypes from 'prop-types';

// Components
import { Container } from '../../components';
import { StyledHero, StyleHeaderWrapper, StyledStatement } from './styles';

export const Hero = ({
  imagePath,
  title,
  subtitle,
  secondaryTitle,
  text,
  ...props
}) => {
  return (
    <StyledHero {...props}>
      <Container as="div">
        <StyleHeaderWrapper {...{ imagePath }}>
          <div>
            {subtitle && <h2>{subtitle}</h2>}
            {title && <h1>{title}</h1>}
          </div>
        </StyleHeaderWrapper>
        <StyledStatement title={secondaryTitle} condensed removeMobileStyles>
          <p>{text}</p>
        </StyledStatement>
      </Container>
    </StyledHero>
  );
};

Hero.propTypes = {
  title: PropTypes.string,
  subtitle: PropTypes.string,
  secondaryTitle: PropTypes.string,
  text: PropTypes.string,
  imagePath: PropTypes.string,
};
