import styled from 'styled-components';
import { Container } from '../container';
import { colors, media, font } from '../../constants/theme';

export const StyledHeader = styled.div`
  @media ${media.notMobile} {
    background-color: ${({ darkTheme }) => (darkTheme ? colors.brand : '#fff')};
  }
  @media ${media.mobile} {
    background-color: ${({ darkTheme }) =>
      darkTheme ? colors.darkBg : 'transparent'};
  } ;
`;

export const StyledContainer = styled(Container)`
  display: flex;
  align-content: center;
  align-items: center;

  @media ${media.condensedNav} {
    height: 80px;
  }
  @media ${media.fullNav} {
    height: 120px;
  }
`;

export const StyledLogo = styled.div`
  @media ${media.condensedNav} {
    height: 45px;
  }
  @media ${media.fullNav} {
    height: 60px;
  }

  svg {
    height: 100%;
  }
`;

export const StyledNav = styled.nav`
  margin-left: auto;

  ul {
    display: flex;
  }

  a {
    display: block;
    font-weight: ${font.weight.navLinks};
    color: ${({ darkTheme }) => (darkTheme ? '#fff' : colors.brand)};
    padding: 1rem 1.2rem;
  }
`;

export const StyledMobileToggle = styled.button`
  margin-left: auto;
  z-index: 60;
`;

export const StyledMobileNav = styled.nav`
  display: ${({ menuOpen }) => (menuOpen ? 'flex' : 'none')};
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: ${colors.brand};
  color: #fff;
  align-items: center;
  justify-content: center;
  z-index: 50;

  ul {
    text-align: center;
  }

  a {
    font-size: 22px;
    display: block;
    font-weight: ${font.weight.navLinks};
    color: #fff;
    padding: 1rem 1.2rem;
  }
`;
