import React, { useState } from 'react';
import PropTypes from 'prop-types';

// Const & Utils
import { useWindowSize } from '../../utils/useWindowSize';
import { mediaSize } from '../../constants/theme';

// Components
import { Link } from 'gatsby';
import { LogoLight, LogoDark } from '../logos';
import {
  StyledHeader,
  StyledContainer,
  StyledLogo,
  StyledNav,
  StyledMobileToggle,
  StyledMobileNav,
} from './styles';

// Data
import data from '../../data/header.json';
// import services from '../../data/services.json';

const createNavSection = (data) => {
  return (
    <ul>
      {data.map((link, index) => (
        <li key={index}>
          <Link to={link.src}>{link.label}</Link>
          {/* {link.children &&
          link.children.length > 0 &&
          createNavSection(link.children)} */}
        </li>
      ))}
    </ul>
  );
};

export const Header = ({ darkTheme }) => {
  const [menuOpen, setMenuOpen] = useState(false);
  const { nav } = data;

  const handleMenuOpen = () => setMenuOpen(!menuOpen);

  // Add services sub links
  // nav.services.children = Object.values(services.data).map((page) => {
  //   return {
  //     label: page.title,
  //     src: page.path,
  //   };
  // });

  const condensedNav = useWindowSize().width <= mediaSize.condensedNav;
  return (
    <StyledHeader darkTheme={darkTheme}>
      <StyledContainer>
        {condensedNav ? (
          <>
            <StyledLogo>
              {darkTheme ? <LogoLight /> : <LogoDark />}
            </StyledLogo>
            <StyledMobileToggle menuOpen={menuOpen} onClick={handleMenuOpen}>
              {menuOpen ? 'X' : 'Menu'}
            </StyledMobileToggle>
            <StyledMobileNav menuOpen={menuOpen}>
              {createNavSection(Object.values(nav))}
            </StyledMobileNav>
          </>
        ) : (
          <>
            <StyledLogo>{darkTheme ? <LogoLight /> : <LogoDark />}</StyledLogo>
            <StyledNav menuOpen={menuOpen} darkTheme={darkTheme}>
              {createNavSection(Object.values(nav))}
            </StyledNav>
          </>
        )}
      </StyledContainer>
    </StyledHeader>
  );
};

Header.propTypes = {
  darkTheme: PropTypes.bool,
};
